/* -------------------------------------------------------------------------- 
	#. Button General
  -------------------------------------------------------------------------- */
.adfy-wishlist-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  font-weight: normal;
  cursor: pointer;
  box-shadow: none;
  border-style: none;
  border-width: 0;
  border-color: transparent;
  transition: all 0.5s ease;
}
.adfy-wishlist-btn:focus, .adfy-wishlist-btn:hover {
  box-shadow: none;
  border-style: none;
  border-width: 0;
  border-color: transparent;
}

.adfy-wishlist-clear-button-style {
  padding: 0;
  margin: 0;
  color: #444444;
  border: none;
  cursor: pointer;
  box-shadow: none;
  border-color: none;
  text-decoration: none;
  background-color: transparent;
}
.adfy-wishlist-clear-button-style:hover {
  color: #444444;
  border: none;
  box-shadow: none;
  text-decoration: none;
  background-color: transparent;
}

/* -------------------------------------------------------------------------- 
	#. Add to Wishlist button
  -------------------------------------------------------------------------- */
.addonify-add-to-wishlist-btn,
.woocommerce ul.products li.product .addonify-add-to-wishlist-btn,
.woocommerce-page ul.products li.product .addonify-add-to-wishlist-btn {
  color: var(--adfy_wishlist_wishlist_btn_text_color);
  background: transparent;
  background-color: var(--adfy_wishlist_wishlist_btn_bg_color);
}
.addonify-add-to-wishlist-btn .icon,
.woocommerce ul.products li.product .addonify-add-to-wishlist-btn .icon,
.woocommerce-page ul.products li.product .addonify-add-to-wishlist-btn .icon {
  color: var(--adfy_wishlist_wishlist_btn_icon_color);
}
.addonify-add-to-wishlist-btn:hover,
.woocommerce ul.products li.product .addonify-add-to-wishlist-btn:hover,
.woocommerce-page ul.products li.product .addonify-add-to-wishlist-btn:hover {
  color: var(--adfy_wishlist_wishlist_btn_text_color_hover);
  background: transparent;
  background-color: var(--adfy_wishlist_wishlist_btn_bg_color_hover);
}
.addonify-add-to-wishlist-btn:hover .icon,
.woocommerce ul.products li.product .addonify-add-to-wishlist-btn:hover .icon,
.woocommerce-page ul.products li.product .addonify-add-to-wishlist-btn:hover .icon {
  color: var(--adfy_wishlist_wishlist_btn_icon_color_hover);
}

/* --------------------------------------------------------------------------
	#. Wishlist modal
  -------------------------------------------------------------------------- */
.addonify-wishlist-modal-is-open {
  overflow: hidden;
}

#addonify-wishlist-modal-wrapper * {
  box-sizing: border-box;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

#addonify-wishlist-modal-wrapper {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s ease;
  background-color: var(--adfy_wishlist_popup_modal_overlay_bg_color);
}
#addonify-wishlist-modal-wrapper .addonify-wishlist-modal-body {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  width: 350px;
  min-height: 350px;
  text-align: center;
  padding: 30px;
  margin: calc((100vh - 300px) / 2) auto 0;
  border-radius: 5px;
  box-sizing: border-box;
  box-shadow: 0px 0px 65px rgba(0, 0, 0, 0.2);
  background-color: var(--adfy_wishlist_popup_modal_bg_color);
  /* Wide */
}
@media (max-width: 575px) {
  #addonify-wishlist-modal-wrapper .addonify-wishlist-modal-body {
    width: 85vw;
    padding: 20px;
  }
}
#addonify-wishlist-modal-wrapper .addonify-wishlist-modal-body .adfy-wishlist-icon-entry .adfy-wishlist-icon {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  font-size: 42px;
  color: var(--adfy_wishlist_popup_modal_icon_color);
  animation: AddonifyWishlistPulse 0.8s infinite linear;
}
#addonify-wishlist-modal-wrapper .addonify-wishlist-modal-body #addonify-wishlist-modal-response .response-text {
  margin: 0;
  padding: 0;
  color: var(--adfy_wishlist_popup_modal_text_color);
}
#addonify-wishlist-modal-wrapper .addonify-wishlist-modal-body .addonify-wishlist-modal-btns {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  row-gap: 20px;
}
#addonify-wishlist-modal-wrapper .addonify-wishlist-modal-body .addonify-wishlist-modal-btns .adfy-wishlist-btn,
#addonify-wishlist-modal-wrapper .addonify-wishlist-modal-body .addonify-wishlist-modal-btns .adfy-wishlist-btn-link {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  font-size: 15px;
  line-height: 1;
  font-weight: normal;
  width: 100%;
  margin: 0;
  padding: 15px;
  height: auto;
  border-radius: 3px;
  color: var(--adfy_wishlist_popup_modal_btn_text_color);
  background-color: var(--adfy_wishlist_popup_modal_btn_bg_color);
  transition: all 0.5s ease;
}
#addonify-wishlist-modal-wrapper .addonify-wishlist-modal-body .addonify-wishlist-modal-btns .adfy-wishlist-btn:hover,
#addonify-wishlist-modal-wrapper .addonify-wishlist-modal-body .addonify-wishlist-modal-btns .adfy-wishlist-btn-link:hover {
  color: var(--adfy_wishlist_popup_modal_btn_text_color_hover);
  background-color: var(--adfy_wishlist_popup_modal_btn_bg_color_hover);
}
#addonify-wishlist-modal-wrapper .addonify-wishlist-modal-body .addonify-wishlist-modal-btns .adfy-wishlist-btn-link {
  text-decoration: none;
}
#addonify-wishlist-modal-wrapper .addonify-wishlist-modal-body .addonify-wishlist-modal-btns .adfy-wishlist-btn-link:hover {
  text-decoration: none;
}
#addonify-wishlist-modal-wrapper .addonify-wishlist-modal-body .addonify-wishlist-modal-btns .adfy-wishlist-btn-link:focus {
  outline: none;
  text-decoration: none;
}

.addonify-wishlist-modal-is-open #addonify-wishlist-modal-wrapper {
  z-index: var(--adfy_wishlist_popup_modal_zindex);
  opacity: 1;
  visibility: visible;
}

#addonify-wishlist-modal-wrapper.loading .addonify-wishlist-modal-body {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
#addonify-wishlist-modal-wrapper.loading .addonify-wishlist-modal-body * {
  display: none;
  opacity: 0;
  visibility: hidden;
}
#addonify-wishlist-modal-wrapper.loading .addonify-wishlist-modal-body::before {
  content: "";
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  background: white url("data:image/svg+xml,%0A%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' width='24' height='24'%3E%3Cpath fill='none' d='M0 0h24v24H0z'/%3E%3Cpath d='M18.364 5.636L16.95 7.05A7 7 0 1 0 19 12h2a9 9 0 1 1-2.636-6.364z'/%3E%3C/svg%3E") no-repeat 50%;
  animation: AddonifyWishlistSpin 0.6s infinite linear;
}

#addonify-wishlist-modal-wrapper.require-login form {
  margin: 0;
  padding: 0;
}

